@import url(https://fonts.googleapis.com/css?family=Exo:400,700);
html,body {
  height: 100%;
}
body {
  background: #0f3854;
  background: radial-gradient(ellipse at center,  #0a2e38  0%, #000000 70%);
  background-size: 100%;
}
.timer-container {
    font-family: 'Share Tech Mono', monospace;
    color: #ffffff;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-shadow: 0 0 20px rgba(255, 178, 10, 1),  0 0 20px rgba(10, 175, 230, 0);
}

.time-display {
	letter-spacing: 0.05em;
    font-size: 80px;
    padding: 115px 0px;
    margin: 0px 0px;
    background-color: #b81df5;
    border: 2px solid #ffb20a;
    border-radius: 50%;
    width: 70%;
    transform: translate(20%, -10%);
    transition-duration: 0.5s;
    transition-timing-function: ease-in-out;

}
.time-display:hover {
    background-color: #b81df5;
    border: 2px solid #ffb20a;
    background-color: #0000;
    cursor: pointer;

}

.timer-button-container{

    font-family: 'Share Tech Mono', monospace;
    color: #fab92c;
    text-align: center;
	position: relative;
	padding-top: 15%;
	justify-content: space-around;
	display: flex;
	font-size: 2rem;

}
@media screen and (max-width: 800px){
    .timer-button-container{

        font-family: 'Share Tech Mono', monospace;
        color: #fab92c;
        text-align: center;
        position: relative;
        padding-top: 10%;
        justify-content: space-around;
        display: block;
        margin-top: 10px;
        font-size: 2rem;        
    }
    .time-display {
        letter-spacing: 0.05em;
        font-size: 80px;
        padding: 100px 10px;
        margin: 0px 0px;
        background-color: #b81df5;
        border: 2px solid #ffb20a;
        border-radius: 50%;
        width:100%;
        align-items: center;
        transform: translate(-3%, 0%);

    
    }
}
.button-container {
    flex: 1 1 auto;
    text-align: center;
    margin: 0px 13px;
    font-size: var(--button-text-size);
    border: 1px solid #ffb20a;
    background-color: #b81df5;
    padding: 10px 27px;
    border-radius: 50px;
    transition-duration: 0.5s;
    transition-timing-function: ease-in-out;
  }
  
  .button-container:hover {
    cursor: pointer;
    color: #ffb20a;
    background-color: #0000;
  }
  @media screen and (max-width: 800px){
    .button-container {
      flex: 1 1 auto;
      text-align: center;
      margin: 15px 13px;
      font-size: var(--button-text-size);
      border: 1px solid #ffb20a;
      background-color: #b81df5;
      padding: 2px 10px;
      border-radius: 50px;
    }
    
  }
.container {
	height: 100vh;
	width: 100vw;
	align-items: center;
	display: flex;
	justify-content: center;
}

*{
    margin: 0px;
    padding: 0px;
}

body{
    font-family: 'Exo', sans-serif;
}


.context {
    width: 100%;
    position: absolute;
    top:50vh;
    
}

.context h1{
    text-align: center;
    color: #fff;
    font-size: 50px;
}


.area{
    background: #4e54c8;  
    background: -webkit-linear-gradient(to left, #8f94fb, #4e54c8);  
    width: 100%;
    height:100vh;
    
   
}

.circles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    -webkit-animation: animate 25s linear infinite;
            animation: animate 25s linear infinite;
    bottom: -150px;
    
}

.circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}


.circles li:nth-child(2){
    left: 10%;
    width: 20px;
    height: 20px;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    -webkit-animation-duration: 12s;
            animation-duration: 12s;
}

.circles li:nth-child(3){
    left: 70%;
    width: 20px;
    height: 20px;
    -webkit-animation-delay: 4s;
            animation-delay: 4s;
}

.circles li:nth-child(4){
    left: 40%;
    width: 60px;
    height: 60px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-duration: 18s;
            animation-duration: 18s;
}

.circles li:nth-child(5){
    left: 65%;
    width: 20px;
    height: 20px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

.circles li:nth-child(6){
    left: 75%;
    width: 110px;
    height: 110px;
    -webkit-animation-delay: 3s;
            animation-delay: 3s;
}

.circles li:nth-child(7){
    left: 35%;
    width: 150px;
    height: 150px;
    -webkit-animation-delay: 7s;
            animation-delay: 7s;
}

.circles li:nth-child(8){
    left: 50%;
    width: 25px;
    height: 25px;
    -webkit-animation-delay: 15s;
            animation-delay: 15s;
    -webkit-animation-duration: 45s;
            animation-duration: 45s;
}

.circles li:nth-child(9){
    left: 20%;
    width: 15px;
    height: 15px;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    -webkit-animation-duration: 35s;
            animation-duration: 35s;
}

.circles li:nth-child(10){
    left: 85%;
    width: 150px;
    height: 150px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-duration: 11s;
            animation-duration: 11s;
}



@-webkit-keyframes animate {

    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}



@keyframes animate {

    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}
